@font-face {
  font-family: 'Product Sans';
  font-style: normal;
  font-weight: 100;
  src: url(https://fonts.gstatic.com/s/productsans/v5/HYvgU2fE2nRJvZ5JFAumwegdm0LZdjqr5-oayXSOefg.woff2) format('woff2');
}

a {
  text-decoration: none;
  color: inherit;
  cursor: none;
}

#nyu:hover {
  color: purple;
}

h1 {
  user-select: none;
  font-family: "Product Sans", sans-serif;
  font-size: 48pt;
  line-height: 50%;
  color: white;
}

h2 {
  user-select: none;
  font-family: "Product Sans", sans-serif;
  font-size: 48pt;
  line-height: 50%;
  color: grey;
}

h2:hover{
  color: white;
}

body {
  height: 100%;
  overflow: hidden;
  background-color: #111111;
}

* {
  padding-left: 10px;
  cursor: none;
}

